'use client'

import React from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useRef, useEffect, useState } from 'react'
import { Crown, Award, Rocket, Target, Shield, Globe, Clock } from 'lucide-react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger)
}

const advantages = [
  {
    icon: Rocket,
    title: 'Launch Faster Than Competitors',
    description: 'Get to market in 30 days while others are still planning',
  },
  {
    icon: Target,
    title: 'Every Pixel Optimized to Convert',
    description: 'Design decisions backed by data, not opinions',
  },
  {
    icon: Shield,
    title: 'Sleep Easy With Bank-Level Security',
    description: 'Enterprise-grade protection for your peace of mind',
  },
  {
    icon: Globe,
    title: 'Built to Scale to Millions',
    description: 'Infrastructure that grows with your success',
  },
  {
    icon: Clock,
    title: 'Help When You Need It Most',
    description: '24/7 support from experts who care about your success',
  },
  {
    icon: Award,
    title: '3x ROI in 90 Days or We Work Free',
    description: 'We guarantee results or keep working until you profit',
    featured: true,
  },
]

export default function CompetitiveEdge() {
  const guaranteeRef = useRef<HTMLDivElement>(null)
  const statsRef = useRef<HTMLDivElement>(null)
  const [activeAdvantage, setActiveAdvantage] = useState(0)
  const [progress, setProgress] = useState(0)

  // GSAP animations
  useEffect(() => {
    if (!guaranteeRef.current) return

    // Floating animation for guarantee badge
    gsap.to(guaranteeRef.current, {
      y: -10,
      duration: 2,
      repeat: -1,
      yoyo: true,
      ease: 'sine.inOut',
    })

    // Rotating rings
    const rings = guaranteeRef.current.querySelectorAll('.rotating-ring')
    rings.forEach((ring, index) => {
      gsap.to(ring, {
        rotation: index % 2 === 0 ? 360 : -360,
        duration: 20 + index * 5,
        repeat: -1,
        ease: 'none',
      })
    })
  }, [])

  // Progress bar animation - cycles through all 6 advantages infinitely
  useEffect(() => {
    const interval = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 100) {
          // Move to next advantage (cycles through all 6)
          setActiveAdvantage((current) => (current + 1) % advantages.length)
          return 0
        }
        return prev + 2 // 2% per 50ms = 2.5 seconds total (faster for better UX)
      })
    }, 50)

    return () => clearInterval(interval)
  }, [])

  // Reset progress when user manually selects an advantage
  useEffect(() => {
    setProgress(0)
  }, [activeAdvantage])

  return (
    <section className="py-24 md:py-32 bg-gradient-to-b from-background via-secondary/30 to-background relative overflow-hidden bg-gradient-mesh texture-noise">
      {/* Background decorations */}
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-accent/20 rounded-full blur-[120px]" />
        <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-accent/10 rounded-full blur-[120px]" />
      </div>

      <div className="container mx-auto px-4 md:px-6 relative z-10 max-w-7xl">
        {/* Header */}
        <div className="text-center mb-12 md:mb-20">
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="inline-flex items-center gap-2 bg-gradient-to-r from-accent/10 to-accent/5 border border-accent/30 rounded-full px-4 md:px-6 py-2 md:py-3 backdrop-blur-md mb-4 md:mb-6"
          >
            <Crown className="w-4 h-4 md:w-5 md:h-5 text-accent" />
            <span className="text-accent text-xs md:text-sm font-medium uppercase tracking-wider">
              Competitive Edge
            </span>
          </motion.div>

          <motion.h2
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6, delay: 0.1 }}
            className="text-3xl md:text-5xl lg:text-6xl xl:text-7xl font-bold text-foreground mb-4 md:mb-6 px-2"
          >
            Why Smart Businesses
            <span className="bg-gradient-to-r from-accent via-accent/80 to-accent bg-clip-text text-transparent block md:inline">
              {' '}
              Choose Us
            </span>
          </motion.h2>

          <motion.p
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6, delay: 0.2 }}
            className="text-base md:text-xl text-muted-foreground max-w-3xl mx-auto leading-relaxed px-2"
          >
            Results that speak louder than promises. Here's what sets us apart and drives real ROI for your business.
          </motion.p>
        </div>

        {/* Active Advantage Showcase */}
        <AnimatePresence mode="wait">
          <motion.div
            key={activeAdvantage}
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -30 }}
            transition={{ duration: 0.5 }}
            className="mb-12 md:mb-16"
          >
            <div className="card-hover-premium bg-gradient-to-br from-card/80 to-card/40 backdrop-blur-lg border border-accent/20 rounded-3xl p-6 md:p-8 lg:p-12 relative overflow-hidden">
              {/* Glow effect */}
              <div className="absolute inset-0 rounded-3xl opacity-20 blur-xl bg-accent/30" />

              <div className="relative z-10 grid md:grid-cols-2 gap-8 md:gap-12 items-center">
                {/* Left: Dynamic Content */}
                <div>
                  <motion.div
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.4 }}
                    className="inline-flex items-center gap-3 mb-4 md:mb-6 p-3 md:p-4 rounded-2xl bg-gradient-to-r from-accent/20 to-accent/10 text-accent"
                  >
                    {React.createElement(advantages[activeAdvantage].icon, {
                      className: "w-6 h-6 md:w-8 md:h-8"
                    })}
                    <span className="text-xl md:text-2xl font-bold">Featured Advantage</span>
                  </motion.div>

                  <motion.h3
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.4, delay: 0.1 }}
                    className="text-2xl md:text-3xl lg:text-4xl font-bold text-foreground mb-3 md:mb-4"
                  >
                    {advantages[activeAdvantage].title}
                  </motion.h3>

                  <motion.p
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.4, delay: 0.2 }}
                    className="text-base md:text-lg text-muted-foreground leading-relaxed mb-6"
                  >
                    {advantages[activeAdvantage].description}
                  </motion.p>

                  {/* Counter indicator - no progress bar */}
                  <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.4, delay: 0.3 }}
                    className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-accent/10 border border-accent/20"
                  >
                    <span className="text-sm font-medium text-accent">
                      {activeAdvantage + 1} of {advantages.length}
                    </span>
                  </motion.div>
                </div>

                {/* Right: Visual Badge */}
                <div className="relative">
                  <motion.div
                    ref={guaranteeRef}
                    key={`icon-${activeAdvantage}`}
                    initial={{ opacity: 0, scale: 0.8, rotateY: -90 }}
                    animate={{ opacity: 1, scale: 1, rotateY: 0 }}
                    exit={{ opacity: 0, scale: 0.8, rotateY: 90 }}
                    transition={{ duration: 0.5 }}
                    className="relative w-48 h-48 md:w-64 md:h-64 mx-auto rounded-full bg-gradient-to-br from-accent/20 to-accent/5 border-2 border-accent/30 flex items-center justify-center backdrop-blur-md"
                  >
                    {React.createElement(advantages[activeAdvantage].icon, {
                      className: "w-16 h-16 md:w-20 md:h-20 text-accent"
                    })}

                    {/* Rotating rings */}
                    <div className="rotating-ring absolute inset-0 rounded-full border border-accent/20" />
                    <div className="rotating-ring absolute inset-4 rounded-full border border-accent/10" />
                  </motion.div>
                </div>
              </div>
            </div>
          </motion.div>
        </AnimatePresence>

        {/* Advantages Grid - All 6 blocks visible */}
        <motion.div
          ref={statsRef}
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.7, delay: 0.4 }}
          className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-6 gap-4 md:gap-6 w-full max-w-7xl mx-auto"
        >
          {advantages.map((advantage, index) => {
            const Icon = advantage.icon
            const isActive = activeAdvantage === index

            return (
              <motion.button
                key={advantage.title}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: 0.5 + index * 0.1 }}
                whileHover={{ scale: 1.05, y: -5 }}
                whileTap={{ scale: 0.95 }}
                onClick={() => {
                  setActiveAdvantage(index)
                  setProgress(0)
                }}
                className={`p-3 md:p-4 rounded-xl border backdrop-blur-md transition-all duration-300 ${isActive
                  ? 'bg-accent/10 border-accent/30 text-accent'
                  : 'bg-card/30 border-border text-muted-foreground hover:border-accent/20'
                  }`}
              >
                <div className="flex flex-col items-center gap-2">
                  <motion.div
                    animate={isActive ? { scale: [1, 1.1, 1] } : {}}
                    transition={{ duration: 0.5 }}
                    className="text-xl md:text-2xl"
                  >
                    <Icon className="w-6 h-6 md:w-8 md:h-8" />
                  </motion.div>
                  <span className="text-xs font-medium text-center leading-tight">
                    {advantage.title}
                  </span>
                </div>

                {/* Progress bar on active item */}
                <div className="mt-2 h-1 rounded-full bg-accent/20 overflow-hidden">
                  <motion.div
                    className="h-full bg-accent rounded-full transition-all"
                    style={{ width: isActive ? `${progress}%` : '0%' }}
                  />
                </div>
              </motion.button>
            )
          })}
        </motion.div>
      </div>
    </section>
  )
}
